ul.progressbar {
  height: 4.6875rem;
  padding: 0;
  counter-reset: step; }

ul.progressbar li {
  list-style-type: none;
  color: #a5a5a5;
  float: left;
  position: relative;
  text-align: center;
  font-size: 0.75rem; }

ul.progressbar li:before {
  content: counter(step);
  counter-increment: step;
  color: #333;
  width: 2.625rem;
  height: 2.625rem;
  line-height: 2.625rem;
  border-radius: 50%;
  display: block;
  text-align: center;
  margin: 0 auto 0.625rem auto;
  background: #d8d8d8; }

ul.progressbar li:after {
  content: "";
  background: #ddd;
  position: absolute;
  width: 100%;
  height: 0.875rem;
  top: 0.875rem;
  right: -50%;
  z-index: -1; }

ul.progressbar li:last-child:after {
  content: none; }

ul.progressbar li.done:before, ul.progressbar li.done:after {
  background: #15db9e;
  color: #fff; }

ul.progressbar li.current:before {
  background: #68bffa;
  color: #fff; }

ul.progressbar li.fail:before {
  background: #e53028; }
